import { useTranslation } from "react-i18next";
import FormBuilderResource from "../../resource/FormBuilder.Resource";

export const NavTabs = () => {
    const { t } = useTranslation(["translation", "contentTranslator"]);

    return <div className="MB5_navTabs">
        <ul className="nav nav-tabs justify-content-center" id="MB5_nav_tabs">
            <li className="nav-item" role="presentation">
                <a className="nav-link active" href="#">{t(
          "contentTranslator:content.structure", FormBuilderResource.Structure_Label
        )}</a>
            </li>
            {/*
            <li className="nav-item" role="logic">
                <a className="nav-link" href="#">Logic</a>
            </li>
            */}
        </ul>
    </div>
}
