import React from "react";
import { useTranslation } from "react-i18next";

export const SelectDropDown = (props) => {
  const { t } = useTranslation(["translation", "contentTranslator"]);

  const { name, options, label, value, cssClass, onChangeHandler } = props;

  const onChange = (event) => {
    onChangeHandler({name, value: event.target.value });
  };

  return (<div className={cssClass}>
    <div className="form-floating mb-3">
      <select className="form-select"
        name={name}
        value={value}
        onChange={onChange}>
        {options && options.map(function(option){
          return <option value={option.value} key={option.value}>{`${t(`contentTranslator:content.${option.label}`, option.label)}`}</option>;
        })}
      </select>
      {label && <label htmlFor={name}>{label}</label>}
    </div>
  </div>);
}