import FormBuilderResource from "../../resource/FormBuilder.Resource";
import FBService from "./FormBuilder.service";
import { useTranslation } from "react-i18next";

export const AddElements = ({ hideAddElementPanel, insertElement }) => {
  const { t } = useTranslation(["translation", "contentTranslator"]);
  const getElementCard = () => {
    let cards = FBService.getCardElementConfig(t);

    return (
      cards &&
      cards.map((card, index) => FBService.getElementCard(card, insertElement))
    );
  };

  return (
    <>
      <div className="">
        <h5 className="text-center mt-1 mb-3">{`${t(
          "contentTranslator:content.Add_Label",
          FormBuilderResource.Add_Label
        )}`}</h5>
        {/* just as a placeholder */}
        <div className="input-group search-input-wrapper px-3 mb-3">
          <label className="input-group-text">
            <i className="fa-solid fa-magnifying-glass"></i>
          </label>
          <input type="text" className="form-control" placeholder={`${t(
        "contentTranslator:content.search", FormBuilderResource.Search_Label
      )}`} />
        </div>

        <div className="">
          <div className="MB5_addElement_nav_tabs">
            <ul
              className="nav nav-tabsjustify-content-center"
              id="MB5_addElement_nav_tabs"
            >
              <li className="nav-item" role="presentation">
                <a className="nav-link active" href="#">{`${t(
                  "contentTranslator:content.QuestionTypes_Label",
                  FormBuilderResource.QuestionTypes_Label
                )}`}</a>
              </li>
              <li className="nav-item" role="presentation">
                <a className="nav-link disabled" href="#">{`${t(
                  "contentTranslator:content.QuestionBlocks_Label",
                  FormBuilderResource.QuestionBlocks_Label
                )}`}</a>
              </li>
              <li className="nav-item" role="presentation">
                <a className="nav-link disabled" href="#">{`${t(
                  "contentTranslator:content.DesignElementsFormat_Label",
                  FormBuilderResource.DesignElementsFormat_Label
                )}`}</a>
              </li>
            </ul>
          </div>

          <div className="modal-container p-3">
            <div className="row">
              <div className="col">
                <h6 className="modal-type-title">
                {`${t(
                  "contentTranslator:content.Headline_Label",
                  FormBuilderResource.Headline_Label
                )}`}
                 
                </h6>
              </div>
            </div>
            <div className="row row-cols-2 row-cols-sm-3 g-4">
              {getElementCard()}
            </div>
          </div>
        </div>
        <button
          role="button"
          className="close-icon--blue"
          onClick={() => hideAddElementPanel()}
        >
          <i className="fa-solid fa-xmark"></i>
        </button>
      </div>
    </>
  );
};
