import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    formData: {
        name: '',
        form_body: {
            fields: []
        }
    },
    formModificationStatus: false
};

export const formBuilderSlice = createSlice({
    name: 'formBuilder',
    initialState,
    reducers: {
        updateFormData: (state, action) => {
            state.formData = action.payload;
        },
        updateFormFields: (state, action) => {
            state.formData.form_body.fields = action.payload;
        },
        updateFormFieldsByElementId:(state, action) => {
            const index =  state.formData.form_body.fields.findIndex((field) => field.id === action.payload.id);
            state.formData.form_body.fields[index] = action.payload;
        },
        updateFormModificationStatus:(state, action) => {
            state.formModificationStatus = action.payload;
        }        
    },
});

export const { updateFormData, updateFormFields, updateFormFieldsByElementId, updateFormModificationStatus } = formBuilderSlice.actions;

export default formBuilderSlice.reducer;