import FormBuilderResource from "../../resource/FormBuilder.Resource";
import Api from "../../api";
import ConfigTextField from "../formEditor/ConfigTextField";

const FBService = {
    preTop: 0,
    getInitialFormDetails() {
        let data;

        Api.get('orders/926')
        .then(response => { 
            data = response.data;
         })
        .catch(error => {
          console.error('There was an error!', error);
        });

        return data;
    },

    updateFormHandler(formData) {
        Api.put('orders/926', formData)
        .then(response => { 
          console.log('Success!');
         })
        .catch(error => {
          console.error('There was an error!', error);
        });
    },

    getSlugLabel(label) {
        let slug = label ? label.trim().toLowerCase().replace(/\W+/g, '_') : '';
        let slug_label = Math.random().toString(36).substring(8) + '_' + slug;

        return slug_label;
    },

    handleMouseMove(e, currentId, popoverId) {
        e.persist()

        let innerDiv = document.getElementById(popoverId);
        let rec = document.getElementById(currentId).getBoundingClientRect();
        let top = rec.top + window.scrollY + 20;
        let currentTop = e.clientY > top ? e.clientY - top : 0;

        if ((currentTop - 20) > FBService.preTop || (currentTop + 20) < FBService.preTop ) {
            FBService.preTop = currentTop;
            innerDiv.style.top = currentTop + 'px';    
        }
     },  

    getFormElement(elementType, counter, t) {
        let newId = counter + 1;

        switch (elementType) {
            case 'text_field':
                return {
                    "field_options": {
                        "description": "",
                        "break_line": true,
                        "number_of_columns": 12,
                        "instructions": "",
                        "detailed_instruction": "",
                        "label": `${t(
                            "contentTranslator:content.label",
                            "Unnamed Text Field"
                          )}`,
                        "keep_on_previous_page": "",
                        "minlength": "",
                        "min_max_length_units": "words",
                        "maxlength": "",
                        "units": "",
                     },
                     "type": "text_field",
                     "is_mandatory": false,
                     "slug": FBService.getSlugLabel(`${t(
                        "contentTranslator:content.label",
                        "Unnamed Text Field"
                      )}`),
                     "regexp": "",
                     "id": newId                
                };

            default:
                return null;
        }
    },

    getElementHtml(index, element, removeElement, duplicateElement) {
        switch (element.type) {
            case 'text_field':
                return <div className="MB5__formField list-group-item" key={`formField-element-${element.id}`}>
                    <ConfigTextField
                        element={element}
                        removeElement={removeElement}
                        duplicateElement={duplicateElement}
                    />
                </div>;

            default:
                break;
        }
    },

    getCardElementConfig(t) {
        let fields = [{
            type: FormBuilderResource.Type_Text_Field,
            title:`${t(
                "contentTranslator:content.Textbox_Label", FormBuilderResource.Textbox_Label
              )}`,
            subTitle:`${t(
                "contentTranslator:content.TextInputField_Label", FormBuilderResource.TextInputField_Label
              )}`
        }];

        return fields;
    },

    getElementCard(card, insertElement) {
        return <div className="col" key={card.type}>
            <div className="card MP5_FormbuilderAddElement" onDoubleClick={() => insertElement(card.type)}>
                <i className="mp5-icons ico-textfield my-2 fa-2x"></i>
                <div className="card-body">
                    <p className="card-sub-title">{card.subTitle}</p>
                    <p className="card-title">{card.title}</p>
                </div>
            </div>
        </div>;
    },
}

export default FBService;
