import React, {useState} from 'react'
import FBService from "../formBuilder/FormBuilder.service";

export const Accordion = (props) => {
    const { type, label, children, removeElement, elementId, duplicateElement, toggleHeading,
        showEllipsis, isChildAccordion, showDraggableArea, cssClass } = props;
    const [isOpen, setOpen] = useState(false);
    let currentId = `MB5__element-${elementId}`;
    let popoverId = `MB5__popover-element-${elementId}`;
    let draggableAreaClass = showDraggableArea ? 'fb-draggable-area' : ''; 
    
    return (
        <div className={`accordion-wrapper ${isChildAccordion ? 'col-12 MP5__configTextField__childAccordion' : 'row g-0'} ${cssClass || ''}`}>
            <div className={`MP5__configTextField__content ${isChildAccordion ? 'col-12' : 'col pe-3'}`}>
                <div className={`accordion ${isOpen ? 'open' : ''}`} id="accordion">
                    <div className={`accordion-header ${draggableAreaClass}`} id="headingOne">
                        <div
                            className={`accordion-title ${isOpen ? "open" : ""}`}
                            onClick={() => setOpen(!isOpen)}>
                            {type && <span className={isChildAccordion ? 'type' : 'textTitleHead'}>{type}</span>}
                            {(label && !isOpen) && <div className='textLabelHead text-truncate ps-4' title={label}>{label}</div>}
                            {(isOpen && !!toggleHeading) && <div className='textLabelHead text-truncate ps-4'>{toggleHeading}</div>}
                        </div>
                    </div>
                    <div className={`accordion-item ${!isOpen ? "collapsed" : ""}`}>
                        <div className={`accordion-content ${isChildAccordion ? 'row px-0' : 'row ps-5 pt-0'}`}>{children}</div>
                    </div>
                </div>
            </div>
           { showEllipsis && <div className='accordion-ellipsis col-auto d-flex align-items-center justify-content-center MB5_side-menu MB5__tooltip'
                id={currentId} onMouseMove={(event) => FBService.handleMouseMove(event, currentId, popoverId)}>
                <div className='ellipsis-center'>
                    <i className="fa fa-ellipsis-vertical"></i>
                </div>
                <div className="tooltiptext" id={popoverId}>
                    <i className="fa-solid fa-trash" onClick={() => removeElement(elementId)}></i>
                    <i className="fa-solid fa-clone" onClick={() => duplicateElement(elementId)}></i>
                </div>
            </div>}
        </div>
    );
};
