export const TextArea = (props) => {
    const { id, name, label, value, placeholder, cssClass, onBlurHandler, onChangeHandler, isDisableEnterKey } = props;

    const onBlur = event => onBlurHandler && onBlurHandler({ name: event.target.name, value: event.target.value });

    const onChange = event => onChangeHandler({name, value: event.target.value });

    const autoResize = () => {
        let textarea = document.getElementById(id);
        textarea.style.height = "";
        textarea.style.height = textarea.scrollHeight + 2 + "px";
    };

    const onKeyDown = (event) => {
        if (isDisableEnterKey && event.key === "Enter") {
            event.preventDefault();
        }
    };
    
    return (<div className={cssClass}>
        <div className={!label ? "form-group mb-3" : "form-floating mb-3"}>
            <textarea
                className="form-control"
                id={id}
                name={name}
                rows="2"
                value={value}
                placeholder={placeholder || ' '}
                onKeyUp={autoResize}
                onChange={onChange}
                onKeyDown={onKeyDown}
                onBlur={onBlur}></textarea>
            {label && <label htmlFor={id}>{label}</label>}
        </div>
    </div>);
}
