import { LimitationUnits, regexOptions, layoutOptions } from "../../constants/TextFiledConstants";
import TextFieldResource from "../../resource/TextField.Resource";
import { TextInput, TextArea, CheckBox, SelectDropDown, Accordion } from "../common";

const FormEditorService = {
  getOptions(filedName) {
    switch (filedName) {
      case "regex_type":
        return regexOptions;
      case "min_max_length_units":
        return LimitationUnits;
      case "number_of_columns":
        return layoutOptions;
      default:
        return [];
    }
  },

  findTypeofRegex(regexp) {
    let selectedRegexType = regexOptions.filter((item) => item.regexValue === regexp);
    if(selectedRegexType?.length === 0 || regexp === "")
      return regexOptions[0]?.value;
    return selectedRegexType[0]?.value;
  },

  getRegexOptionData(fields, selectedOption) {
    let selectedRegexType = regexOptions.filter((item) => item.value === selectedOption);
    let { field_options, ...rest } = fields;
    let updated_rest = { ...rest, regexp: selectedRegexType[0].regexValue };
    let updatedFields = { ...fields, ...updated_rest };

    return updatedFields;
  },

  onDecreaseHandler(value) {
    let newValue = parseInt(value) || 0;

    if (newValue > 0) {
      newValue = parseInt(value) - 1;
    }

    return newValue.toString();
  },

  onIncreaseHandler(value) {
    let newValue = parseInt(value) || 0;
    newValue = parseInt(newValue) + 1;

    return newValue.toString();
  },

  checkInternalNameValidation(val) {
    return val.replace(/[^A-Za-z0-9\_]/gi, "").toLowerCase();
  },

  updateOnBlurHandler(data, fields) {
    let updatedData, maxValue;
    let fieldOptions = { ...fields.field_options };
    let { name, value } = data;
   
    switch (name) {
      case TextFieldResource.MinLength:
        maxValue = parseInt(value) > parseInt(fieldOptions.maxlength) && !!fieldOptions.maxlength ? value : fieldOptions.maxlength;
        updatedData = { ...fields, field_options: { ...fieldOptions, minlength: value, maxlength: maxValue } };
        break;
      case TextFieldResource.MaxLength:
        maxValue = parseInt(fieldOptions.minlength) > parseInt(value) ? fieldOptions.minlength : value;
        updatedData = { ...fields, field_options: { ...fieldOptions, maxlength: maxValue } };
        break;
      default:
        updatedData = { ...fields };
    }

    return updatedData;
  },

  updateOnChangeHandler(data, fields, setRegexType) {
    let updatedData;
    let fieldOptions = { ...fields.field_options }; 
    let { name, value } = data;

    switch (name) {
      case TextFieldResource.FieldName_Label:
      case TextFieldResource.FieldName_Description:
      case TextFieldResource.FieldName_Instructions:
      case TextFieldResource.FieldName_DetailedInstruction:
      case TextFieldResource.MinMaxLengthUnits:
      case TextFieldResource.FieldName_KeepOnPreviousPage:
      case TextFieldResource.FieldName_BreakLine:
      case TextFieldResource.FieldName_Units:
      case TextFieldResource.NumberOfColumns:
      case TextFieldResource.MinLength:
      case TextFieldResource.MaxLength:
        updatedData = { ...fields, field_options: { ...fieldOptions, [name]: value } };
        break;
      case TextFieldResource.FieldName_IsMandatory:
        updatedData = { ...fields, [name]: value };
        break;
      case TextFieldResource.Regexp:
        let selectedRegexpType = FormEditorService.findTypeofRegex(value);
        updatedData = { ...fields, [name]: value };
        setRegexType(selectedRegexpType);        
        break;
      case TextFieldResource.FieldName_Slug:
        updatedData = { ...fields, [name]: FormEditorService.checkInternalNameValidation(value) };
        break;
      case TextFieldResource.RegexType:
        let updated_fields = { ...fields, field_options: { ...fieldOptions, minlength: '', maxlength: '', min_max_length_units: '', units: '' } };
        updatedData = FormEditorService.getRegexOptionData(updated_fields, value, name);
        setRegexType(value);
        break;
      default:
        updatedData = { ...fields };
    }

    return updatedData;
  },

  getBasicDetails(fields, onChangeHandler, onBlurHandler, t) {
    return (<>
      <TextArea
        onBlurHandler={onBlurHandler}
        onChangeHandler={onChangeHandler}
        id={`${TextFieldResource.FieldName_Label}_${fields.id}`}
        name={TextFieldResource.FieldName_Label}
        value={fields?.field_options?.label}
        isDisableEnterKey={true}
        cssClass="col-12"
      />
      
      <CheckBox
        onChangeHandler={onChangeHandler}
        id={`${TextFieldResource.FieldName_IsMandatory}_${fields.id}`}
        name={TextFieldResource.FieldName_IsMandatory}
        label={`${t(
          "contentTranslator:content.manditory",TextFieldResource.Mandatory
        )}`}
        value={fields?.is_mandatory}
      />
    </>);
  },

  getInstructionsForClickworker(fields, onChangeHandler, onBlurHandler, t) {
    return (<Accordion
      type={`${t(`contentTranslator:content.EnterInstructionsForClickworker`, TextFieldResource.EnterInstructionsForClickworker)}`}
      isChildAccordion
      cssClass='mp5__configTextField__clickworker-instruction'>
      
    <TextArea
      onBlurHandler={onBlurHandler}
      onChangeHandler={onChangeHandler}
      id={`${TextFieldResource.FieldName_Description}_${fields.id}`}
      name={TextFieldResource.FieldName_Description}
      label={`${t(
        "contentTranslator:content.description", TextFieldResource.Description
      )}`}
      value={fields?.field_options?.description}
      placeholder= {TextFieldResource.Description}
      cssClass="col-12"
    />

    <TextArea
      onBlurHandler={onBlurHandler}
      onChangeHandler={onChangeHandler}
      id={`${TextFieldResource.FieldName_Instructions}_${fields.id}`}
      name={TextFieldResource.FieldName_Instructions}
      label={`${t(
        "contentTranslator:content.instruction", TextFieldResource.Instructions
      )}`}
      value={fields?.field_options?.instructions}
      placeholder={TextFieldResource.Instructions}
      cssClass="col-12"
    />

    <TextArea
      onBlurHandler={onBlurHandler}
      onChangeHandler={onChangeHandler}
      id={`${TextFieldResource.FieldName_DetailedInstruction}_${fields.id}`}
      name={TextFieldResource.FieldName_DetailedInstruction}
      label={`${t(
        "contentTranslator:content.detailedInstruction", TextFieldResource.DetailedInstructions
      )}`}
      value={fields?.field_options?.detailed_instruction}
      placeholder={TextFieldResource.DetailedInstructions}
      cssClass="col-12"
    />
  </Accordion>)
  },

  getValidateAnswerForSpecificFormat(fields, regexType, onChangeHandler, onBlurHandler, onIncreaseHandler, onDecreaseHandler,t) {
    return (<Accordion
      type={`${t(`contentTranslator:content.ValidateAnswerForSpecificFormat`, TextFieldResource.ValidateAnswerForSpecificFormat)}`}
      cssClass='mp5__configTextField__specific-format'
      isChildAccordion
    >
      <SelectDropDown
        options={FormEditorService.getOptions(TextFieldResource.RegexType)}
        name={TextFieldResource.RegexType}
        label={`${t(
          "contentTranslator:content.inputFieldType", TextFieldResource.InputFieldType
        )}`}
        value={regexType}
        onChangeHandler={onChangeHandler}
        cssClass="col-6"
      />

      {regexType !== regexOptions[3].value && <TextInput
        label={`${t(
          "contentTranslator:content.units", TextFieldResource.Units
        )}`}
        value={fields?.field_options?.units}
        type="text"
        id={`${TextFieldResource.FieldName_Units}_${fields.id}`}
        name={TextFieldResource.FieldName_Units}
        onBlurHandler={onBlurHandler}
        onChangeHandler={onChangeHandler}
        cssClass="col-6"
      />}
                
      {regexType === regexOptions[0].value && <><span> {t("contentTranslator:content.limitation", TextFieldResource.Limitation_Label)}</span>
      <div className="col-12 col-lg-6 row pe-0">
        <TextInput
        label={`${t("contentTranslator:content.min", TextFieldResource.Min)}`}
        value={fields?.field_options?.minlength}
        type="number"
        id={`${TextFieldResource.MinLength}_${fields.id}`}
        name={TextFieldResource.MinLength}
        onBlurHandler={onBlurHandler}
        onChangeHandler={onChangeHandler}
        showIncreaseDecreaseIndicator={true}
        onIncreaseHandler={onIncreaseHandler}
        onDecreaseHandler={onDecreaseHandler} 
        cssClass="col-6 col-lg-3"
      />
      <TextInput
        label={`${t(
          "contentTranslator:content.max",TextFieldResource.Max
          )}`}
        value={fields?.field_options?.maxlength}
        type="number"
        id={`${TextFieldResource.MaxLength}_${fields.id}`}
        name={TextFieldResource.MaxLength}
        onBlurHandler={onBlurHandler}
        onChangeHandler={onChangeHandler}
        showIncreaseDecreaseIndicator={true}
        onIncreaseHandler={onIncreaseHandler}
        onDecreaseHandler={onDecreaseHandler}
        cssClass="col-6 col-lg-3 pe-0 pe-lg-2"
      />
          
      <SelectDropDown
        options={FormEditorService.getOptions(TextFieldResource.MinMaxLengthUnits)}
        name={TextFieldResource.MinMaxLengthUnits}
        label={`${t(
          "contentTranslator:content.limitationUnits", TextFieldResource.LimitationUnits
          )}`}
        value={fields?.field_options.min_max_length_units}
        onChangeHandler={onChangeHandler}
        cssClass="col-12 col-lg-6 pe-0"
      /></div>
      </>}

      <TextInput
        label={`${t(
          "contentTranslator:content.regexp", TextFieldResource.Regexp_Label
          )}`}
        value={fields?.regexp}
        type="text"
        id={`${TextFieldResource.Regexp}_${fields.id}`}
        name={TextFieldResource.Regexp}
        onBlurHandler={onBlurHandler}
        onChangeHandler={onChangeHandler}
        cssClass="col-12"
      />
    </Accordion>);
  },

  getAdvancedFunctionsLayout(fields, onChangeHandler, onBlurHandler,t) {
    return (<Accordion
      type={`${t(`contentTranslator:content.AdvancedFunctionsLayout`, TextFieldResource.AdvancedFunctionsLayout)}`}
      cssClass='mp5__configTextField__advanced-layout'
      isChildAccordion>
      <TextInput
        label={`${t(
          "contentTranslator:content.internalName", TextFieldResource.InternalName
          )}`}
        value={fields?.slug}
        type="text"
        id={`${TextFieldResource.FieldName_Slug}_${fields.id}`}
        name={TextFieldResource.FieldName_Slug}
        onChangeHandler={onChangeHandler}
        onBlurHandler={onBlurHandler}
        cssClass="col-12"
      />
    
      <div className="row">
      <CheckBox
        onChangeHandler={onChangeHandler}
        id={`${TextFieldResource.FieldName_KeepOnPreviousPage}_${fields.id}`}
        name={TextFieldResource.FieldName_KeepOnPreviousPage}
        label={`${t(
          "contentTranslator:content.keepElementOnPreviousPage", TextFieldResource.KeepElementOnPreviousPage
          )}`}
        value={fields?.field_options?.keep_on_previous_page}
        cssClass="col-6"  
      />
      
      <CheckBox
        onChangeHandler={onChangeHandler}
        id={`${TextFieldResource.FieldName_BreakLine}_${fields.id}`}
        name={TextFieldResource.FieldName_BreakLine}
        label={`${t(
          "contentTranslator:content.startNewLineAfterElement", TextFieldResource.StartNewLineAfterElement
          )}`}
        value={fields?.field_options?.break_line}
        cssClass="col-6"  
      />
      </div>

      <SelectDropDown
        options={FormEditorService.getOptions(TextFieldResource.NumberOfColumns)}
        name={TextFieldResource.NumberOfColumns}
        label={`${t(
          "contentTranslator:content.layout", TextFieldResource.Layout
          )}`}
        value={fields?.field_options?.number_of_columns}
        onChangeHandler={onChangeHandler}
        cssClass="col-6"
      />

    </Accordion>);
  },
};

export default FormEditorService;
