import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import {
  updateFormFieldsByElementId,
  updateFormModificationStatus,
} from "../../redux/features/formBuilder/formBuilderSlice";
import { Accordion } from "../common";
import FormEditorService from "./FormEditor.service";
import { regexOptions } from "../../constants/TextFiledConstants";
import TextFieldResource from "../../resource/TextField.Resource";

const ConfigTextField = (props) => {
  const { t } = useTranslation(["translation", "contentTranslator"]);

  const { element } = props;
  const [fields, setFields] = useState(element);
  const [regexType, setRegexType] = useState(regexOptions[0].value);
  const dispatch = useDispatch();

  const onBlurHandler = (data) => {
    let updatedField = FormEditorService.updateOnBlurHandler(data, fields);

    if (JSON.stringify(updatedField) !== JSON.stringify(fields)) {
      saveElementForm(updatedField);  
    }
  };

  const onIncreaseHandler = (name, value) => {
    let fieldOptions = { ...fields.field_options }; 
    let updatedValue = FormEditorService.onIncreaseHandler(value);
    let updatedField = { ...fields, field_options: { ...fieldOptions, [name]: updatedValue } };

    saveElementForm(updatedField);
  };

  const onDecreaseHandler = (name, value) => {
    let fieldOptions = { ...fields.field_options }; 
    let updatedValue = FormEditorService.onDecreaseHandler(value);
    let updatedField = { ...fields, field_options: { ...fieldOptions, [name]: updatedValue } };

    saveElementForm(updatedField);
  };

  const onChangeHandler = (data) => {
    let updatedField = FormEditorService.updateOnChangeHandler(data, fields, setRegexType);

    saveElementForm(updatedField);
  };

  const saveElementForm = (data) => {
    setFields(data);
    dispatch(updateFormFieldsByElementId(data));
    dispatch(updateFormModificationStatus(true));
  };

  const renderAccordion = () => {
    return (
      <Accordion
        type={`${t(
          `contentTranslator:content.${fields?.type}`, fields?.type
        )}`}
        label={fields?.field_options?.label}
        removeElement={props.removeElement}
        duplicateElement={props.duplicateElement}
        elementId={element.id}
        toggleHeading={`${t( "contentTranslator:content.title", TextFieldResource.Title)}`}
        showEllipsis
        showDraggableArea
      >
        {FormEditorService.getBasicDetails(fields, onChangeHandler, onBlurHandler, t)}
        {FormEditorService.getInstructionsForClickworker(fields, onChangeHandler, onBlurHandler, t)}
        {FormEditorService.getValidateAnswerForSpecificFormat(fields, regexType, onChangeHandler, onBlurHandler, onIncreaseHandler, onDecreaseHandler, t)}
        {FormEditorService.getAdvancedFunctionsLayout(fields, onChangeHandler, onBlurHandler, t)}
      </Accordion>
    );
  };
  return <div className="MP5__configTextField__wrapper">{renderAccordion()}</div>;
};

export default ConfigTextField;
