import React from "react";
import {useState, useEffect} from 'react';
import {NavTabs} from '../navTabs/NavTabs';
import { AddElements } from "./AddElements";
import FBService from "./FormBuilder.service";
import Api from "../../api";
import { useSelector, useDispatch } from "react-redux";
import { updateFormData, updateFormFields, updateFormModificationStatus } from "../../redux/features/formBuilder/formBuilderSlice";
import Sortable from 'sortablejs';
import { useTranslation } from "react-i18next";

export const FormBuilder = () => {
  const { t } = useTranslation(["translation", "contentTranslator"]);

  const formData = useSelector((state) => state.formBuilder.formData);
  const formModificationStatus = useSelector((state) => state.formBuilder.formModificationStatus);
  const dispatch = useDispatch();
  const [showAddIcon, setShowAddIcon] = useState(true);
  const [idCounter, setIdCounter] = useState(0);
  const [oldIndex, setOldIndex] = useState();
  const [newIndex, setNewIndex] = useState();

  useEffect(() => {
    Api.get('orders/926')
      .then(response => { 
        let fields = response.data && response.data.form_body && response.data.form_body.fields;
        let lastCounter = fields && Math.max(...fields.map(field => field.id), 0); 

        dispatch(updateFormData(response.data));
        setIdCounter(lastCounter || 0);
       })
      .catch(error => {
        console.error('There was an error!', error);
      });
    
      let fbFormElement = document.getElementById('fbFormElement');
      new Sortable(fbFormElement, {
        group: "shared",
        animation: 150,
        ghostClass: 'background-class',
        handle: '.fb-draggable-area',
        onEnd: function (evt) {
          setOldIndex(evt.oldIndex)
          setNewIndex(evt.newIndex);
        },
      });
  }, []);

  useEffect(() => {
    newIndex && dragEndHandler();
  }, [newIndex]);

  useEffect(() => {
    const interval = setInterval(() => saveFormHandler(), 5000);

    return () => clearInterval(interval);
  }, [formModificationStatus]);

  const showAddElementPanel = () => {
      setShowAddIcon(false);
  };

  const hideAddElementPanel = () => {
    setShowAddIcon(true);
  };

  const insertElement = (selectedElement) => {
    let newElement = FBService.getFormElement(selectedElement, idCounter, t);
    let formFields = formData.form_body.fields;
    let fields = [...formFields, newElement];

    setIdCounter(idCounter + 1);
    dispatch(updateFormFields(fields));
    dispatch(updateFormModificationStatus(true));
  };

  const removeElement = (elementId) => {
    let formFields = formData.form_body.fields;
    let restFields = formFields.filter((field) => { return field.id !== elementId; });
    let fields = [...restFields];

    dispatch(updateFormFields(fields));
    dispatch(updateFormModificationStatus(true));
  };

  const duplicateElement = (elementId) => {
    let startIndex;
    let formFields = [...formData.form_body.fields];
    let cloneElement = formFields.filter((field, index) => {
      if(field.id === elementId) startIndex = index + 1;
      
      return field.id === elementId;
    })[0];
    let newElementId = idCounter + 1;
    let newElementSlug = FBService.getSlugLabel(cloneElement.field_options.label);
    let newElement = { ...cloneElement, slug: newElementSlug, id: newElementId };

    formFields.splice(startIndex, 0, newElement);
    setIdCounter(newElementId);
    dispatch(updateFormFields(formFields));
    dispatch(updateFormModificationStatus(true));
  };

  const dragEndHandler = () => {
    let formFields = formData.form_body.fields;
    let copyListItems = [...formFields];
    let dragItemContent = copyListItems[oldIndex];

    copyListItems.splice(oldIndex, 1);
    copyListItems.splice(newIndex, 0, dragItemContent);
    setNewIndex(null);
    setOldIndex(null);
    dispatch(updateFormFields(copyListItems));
    dispatch(updateFormModificationStatus(true));
  };

  const saveFormHandler = () => {
    if (formModificationStatus) {
      FBService.updateFormHandler(formData);
      dispatch(updateFormModificationStatus(false));
    }
  };
  
  const renderAddElementContainer = () => {
      return <div className="col-auto MP5__addElement__container fade show active">
          <AddElements
            hideAddElementPanel={hideAddElementPanel}
            insertElement={insertElement}
          />
      </div>;
  };

  const renderFormContainer = () => {
    let sidePanelOpenClass = !showAddIcon ? "side-panel-open" : "";
    let fields = formData && formData.form_body && formData.form_body.fields;

    return <div className={`MB5__formContainer col ${sidePanelOpenClass}`}>
        <form className="MB5__form list-group" id="fbFormElement">
          {fields && fields.map((element, index) => (
              FBService.getElementHtml(index, element, removeElement, duplicateElement)
          ))}
        </form>
    </div>;
  };

  return <div>
      <NavTabs />

      <div className="container-fluid">
        <div className="row">
          {renderFormContainer()}
          {!showAddIcon && renderAddElementContainer()}
        </div>
      </div>

      {showAddIcon && <button role="button" className="add-icon--blue" onClick={() => showAddElementPanel()}><i className="fa-solid fa-plus"></i></button>}
  </div>;
}
