export const TextInput = (props) => {
  const { id, type, name, label, value, placeholder, cssClass, isExternalLabel, showInputTextGroup,
    onChangeHandler, onBlurHandler, showIncreaseDecreaseIndicator, onIncreaseHandler, onDecreaseHandler } = props;

  const onBlur = (name, value) => {
    onBlurHandler && onBlurHandler({ name, value });
  };

  const onChange = (event) => {
    return onChangeHandler({name, value: event.target.value });
  };

  const renderInput = () => {
    return (<input
      type={type}
      id={id}
      name={name}
      value={value}
      className="form-control"
      placeholder={placeholder || ' '}
      onChange={onChange}
      onBlur={(event) => onBlur(event.target.name, event.target.value)}
    />);
  };

  const renderInputTextGroup = () => {
    return (<div className="input-group mb-3">
      <span className="input-group-text">/</span>
      {renderInput()}
      <span className="input-group-text">/</span>
    </div>);
  };

  const renderIncreaseDecreaseIndicator = () => {
    return (<>
      <a href="#" className="increase text-muted"
        onMouseLeave={() => onBlur(name, value)}
        onClick={() => onIncreaseHandler(name, value)}>
        <i className="fa-solid fa-chevron-up"></i></a>
      <a href="#" className="decrease text-muted"
        onMouseLeave={() => onBlur(name, value)}
        onClick={() => onDecreaseHandler(name, value)}>
        <i className="fa-solid fa-chevron-down"></i></a>
    </>);
  };

  const renderTextInput = () => {
    return (<div className={(isExternalLabel || !label) ? "form-group mb-3" : "form-floating mb-3"}>
      {isExternalLabel && label && (<label htmlFor={id}>{label}</label>)}
      {renderInput()}
      {showIncreaseDecreaseIndicator && renderIncreaseDecreaseIndicator()}
      {!isExternalLabel && label && <label htmlFor={id}>{label}</label>}
    </div>);
  };

  return (<div className={cssClass}>
    {!showInputTextGroup && renderTextInput()}
    {showInputTextGroup && renderInputTextGroup()}
  </div>);
};
